  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 10%);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  opacity: 0;
  transition: all 0.4s ease;
}
.bg-layer-hover:hover::before {
  opacity: 1;
}
